import React from 'react';
import './Login.css'
export default class Login extends React.Component {
  constructor(props) {
    super();
    this.state = {
      name: "LOGIN HERE",
      email: "",
      password: "",
      errorEmail: "",
      errorPassword: "",
    }
  }


  _submit = () => {

    if (this.state.email === '') {
      this.setState({ errorEmail: "*Please enter Email." })
    }
    if (this.state.password === '') {
      this.setState({ errorPassword: "*Please enter Password." })
    }
    else {
      alert("Submitted Successfully.")
    }

  }
  _RegisterHere = () => {
    this.props.history.push('/SignUp')
  }

  render() {
   
    return (

      <div className="mainDiv">
        <div className="header">
          <h1 className="headerleftButton1">{this.state.name}</h1>
        </div>
        <div className="loginCard">
          <label className="lableEmail">EMAIL/PHONE</label>
          <input
            className="inputEmail"
            placeholder="EMAIL/PHONE"
            onChange={(event) => { this.setState({ email: event.target.value }) }}
          />
          <div className="errorDiv">
          {this.state.errorEmail !=='' ?  <p className="errorMessage">{this.state.errorEmail}</p> : null}
          </div>
     

          <br></br>
          <label className="lablePassword">PASSWORD</label>
          <input
            type="password"
            className="inputEmail"
            // className="inputPassword"
            placeholder="PASSWORD"
            onChange={(event) => { this.setState({ password: event.target.value }) }}
          />
        <div className="errorDiv">
        {this.state.errorPassword !=="" ? <p className="errorMessage">{this.state.errorPassword}</p>: null }
        </div>
          <br></br>
          <button
            className="button"
            onClick={() => this._submit()}
          > LOGIN </button>
          <br></br>
          <div>
            <button
              className="registerHere"
              onClick={() => this._RegisterHere()}
            > Not Registered/Signup Here </button>
          </div>
        </div>

      </div>
    );
  }
}
