import React from 'react';
import './HomePage.css'
import {connect} from 'react-redux'
import {buyBook} from '../Redux/bookAction'
import { CircleSlider } from "react-circle-slider";

   function HomePage (props) {       
     
       return (
          <div className="mainDiv">
           
              <h1>Available BOOK ={props.numberOfBooks}% </h1>  
            <div>
                     <CircleSlider        // https://www.npmjs.com/package/react-circle-slider
                     value={props.numberOfBooks}
                     size={250}
                     shadow={false}                
                     knobColor="#ff5722"
                     // disabled={true}
                     showTooltip={true}
                     tooltipSize={50}    
                     min={0}    
                     max={100}            
                     showPercentage={true}
                     //  progressColor="#FDB11B"
                     />
               </div>
               <button 
               onClick={props.buyBook}              
               className='buttonStyle'>Decrease quantity by click me </button>
            </div>
       );   
 }

 const mapStatetoProps = (state)=>{
      return {
         numberOfBooks:state.numberOfBooks
      }
 }
 const mapDispatchtoProps = (dispatch) =>{
      return{
         buyBook:function(){            
            dispatch( buyBook() );
            
         }
      }
 }

 export default connect(mapStatetoProps,mapDispatchtoProps) (HomePage);