import React,{Component} from 'react';
import './SignUp.css'
  export default class SignUp extends Component {  
    constructor(props) {
      super();
      this.state = {
        title: "SIGNUP HERE",
        name:"",
        nameError:"",

        email: "",
        errorEmail: "",

        phoneNumber:"",
        errorPhoneNumber:"",

        password: "",
        errorPassword: "",

        confirmPassword:"",      
        errorConfirmPassword: "",
      }
    }
    
    
    _submit=()=>{
      if(this.state.name==='')
      {
        this.setState({nameError:"*Please enter name"})
       }
        if( this.state.email==='')
        {
          this.setState({ errorEmail: "*Please enter email." })
          }
            if(this.state.phoneNumber==='')
           {
             this.setState({ errorPhoneNumber: "*Please enter phone number." })
             } 
               if( this.state.password==='')
               {
                 this.setState({ errorPassword: "*Please enter password." })
                 }    
                  if(this.state.confirmPassword==='')
                 {
                   this.setState({ errorConfirmPassword: "*Please enter confirm password." })
                  }
              else
              {
                this.props.history.push('/Login')
              }      
    }
    render() {
     console.log("pankaj=-=-=-=->>>>",this.state)
       return (
          <div className="firstDiv">
             <div className="header">
       <h1 className="headerleftButton1">{this.state.title}</h1>
             </div>

             <div className="signUpCard">
               <label className="lableEmail">Name</label>
               <input
                    className="inputName"
                    placeholder="Name"
                    onChange={(event) => { this.setState({ name: event.target.value }) }}
                />
                <div className="errorDiv">
                {this.state.nameError !=="" ? <p className="errorMessage">{this.state.nameError}</p>: null }
                  </div>
                 <br></br>
                 <label className="lableEmail">Email</label>
               <input
                    className="inputEmail"
                    placeholder="Email"
                    onChange={(event) => { this.setState({ email: event.target.value }) }}
                />
                   <div className="errorDiv">
                   {this.state.errorEmail !=="" ? <p className="errorMessage">{this.state.errorEmail}</p>: null }
                   </div>
                
                 <br></br>
                 <label className="lableEmail">Phone Number</label>
               <input
                    className="inputPhone"
                    placeholder="Phone Number"
                    onChange={(event) => { this.setState({ phoneNumber: event.target.value }) }}
                />
                 <div className="errorDiv">
                  {this.state.errorPhoneNumber !=="" ? <p className="errorMessage">{this.state.errorPhoneNumber}</p>: null }
                  </div>
                 <br></br>
                 <label className="lableEmail">Password</label>
               <input
                    className="inputPassword"
                    placeholder="Password"
                    type="Password"
                    onChange={(event) => { this.setState({ password: event.target.value }) }}
                />
                 <div className="errorDiv">
                  {this.state.errorPassword !=="" ? <p className="errorMessage">{this.state.errorPassword}</p>: null }
                  </div>
                 <br></br>
                 <label className="lableEmail">Confirm Password</label>
               <input
                    className="inputConfirmPassword"
                    placeholder= "Confirm Password"
                    type="Password"
                    onChange={(event) => { this.setState({ confirmPassword: event.target.value }) }}
                />
                 <div className="errorDiv">
                  {this.state.errorConfirmPassword !=="" ? <p className="errorMessage">{this.state.errorConfirmPassword}</p>: null }
                  </div> 
                  <br></br>
                 <button
                        className="button"
                        onClick={() => this._submit()}
                    > SignUp </button>
                    <br></br>
                    <div>
                        <button
                        className="registerHere"
                        onClick={() =>{this.props.history.push('/Login')}}
                        >Already Registered/Login Here </button>
                    </div>

             </div>
                
          </div>
       );
    }
 }